import { useBox } from 'use-cannon';

const Floor = props =>{

    const [ref,api] = useBox(()=>({
        args: [250,1,100],
        ...props        
    }))
    return (

        <mesh {...props} ref={ref} api={api} receiveShadow>
            <boxBufferGeometry args={[250,1,100]}/>
            <meshPhysicalMaterial transparent opacity={[0.2]}/>
        </mesh>

    )
}

export default Floor;