import { IoArrowForward, IoArrowBack } from 'react-icons/io5';
import { useState, useRef } from 'react';
const HeroSection = styled.div`
    height: 100vh;
    /* max-height: 1100px; */
    position: relative;
    overflow: hidden;    
`
const HeroWrapper = styled.div`
    width:100%;
    height: 100%;   
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;    
`

const HeroSlide = styled.div`
    z-index: 1;
    width: 100%;
    height: 100%;
`;
const HeroSlider = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    &::before {
        content: '';
        z-index: 2;
        width: 100%;
        height: 100vh;
        bottom: 0vh;
        left: 0;
        overflow: hidden;
        opacity: 0.4;
        background: linear-gradient(0deg, 
            rgba(0,0,0,0.2) 0%,
            rgba(0,0,0,0.4) 50%,
            rgba(0,0,0,0.6) 100%)
        }
`;
