const HeroImage = styled.img`
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
`;
const Button = styled.button`

`;

const arrowButtons = css`
    width: 50px;
    height: 50px;
    color: #fff;
    cursor: pointer;
    background: #000d1a;
    border-radius: 50px;
    padding: 10px;
    user-select: none;
    margin-right: 2rem;
    transition: 0.3s;

    &:hover{
        background: #cd853f;
        transform : scale(1.05);
    }
`

const NextArrow = styled(IoArrowForward)`
    ${arrowButtons}
`
const PrevArrow = styled(IoArrowBack)`
    ${arrowButtons}
`

const Arrow = styled(IoMdArrowRoundForward)``;

const SliderButtons = styled.div`
    position:absolute;
    bottom: 50px;
    right: 50px;
    display: flex;
    z-index: 10;
`;

const Hero = ({slides}) =>{

    const [current, setCurrent] = useState(0)
    const length = slides.length;
    const timer = useRef(null)

    const nextSlide = () => {
        setCurrent(current === length - 1 ? 0 : current + 1 )
    }

    const prevSlide = () => {
        setCurrent(current === 0 ? length - 1 : current - 1 )
    }    
    return(
        <HeroSection>
            <HeroWrapper>
                {slides.map((slide,index)=>{
                    return(
                        <HeroSlide key={index}>
                            {
                                index === current && (
                                <HeroSlider>
                                  <HeroImage src={slide.image} alt={slide.alt}/>
                                    <HeroContent>
                                        <HeroText><h1>{slide.title}</h1><h2>{slide.price}</h2><h2>{slide.price}</h2></HeroText>
                                        
                                        
                                        {/* <Button to={slide.path} primary="true" >
                                            {slide.label}
                       
                                        </Button> */}
                                        {/* <Arrow/> */}
                                    </HeroContent>
                                </HeroSlider>

                                )
                            }

                        </HeroSlide>
                    )
                })}
                <SliderButtons>
                    <PrevArrow onClick={prevSlide}/>
                    <NextArrow onClick={nextSlide}/>
                </SliderButtons>
            </HeroWrapper>
        </HeroSection>
        )

}

export default Hero;