import { Suspense } from "react"
import Model from './Model'
import BoundingBox from './BoundingBox';
import Dragable from './Dragable';
const Cars = () => {
    return(
    <Suspense fallback={null}>
        <Dragable transformGroup >        
          <BoundingBox  position={[-30,1,0]} offset={[10,1,5]} dims={[10,10,16]}>
            <Model 
              path='/tesla/scene.gltf' 
              scale={new Array(3).fill(3.2)}
              rotation={[0,-135,0]}
              // position={[0,5,0]}
              />              
          </BoundingBox>
            
        </Dragable>
        <Dragable transformGroup>
          <BoundingBox position={[20,2,0]} offset={[-10,1,5]} dims={[15,5,5]}>
            <Model 
              path='/mustang/scene.gltf' 
              scale={new Array(3).fill(0.02)}
              rotation={[0,-275,0]}
              // position={[20,-1,-10]}
              />
          </BoundingBox>
        </Dragable>  
    </Suspense>
        )
}

export default Cars;