
import * as THREE from 'three';
import state from '../state'
const ColorPicker = () =>{
    const handleClick = e =>{
      if(!state.activeMesh){
        return;
      }
      state.activeMesh.material.color = new THREE.Color(e.target.style.background);
    }  
    return(
      <div style={{
        position:"absolute", 
        zIndex: 1,
        display:"flex",
        // flexDirection: "center",
        // justifyContent:"center",
        // alignItems:"center"
        }}>
      <div
      onClick={handleClick} 
      style={{
        background: 'blue',
        height: 50,
        width:50,
        borderRadius:50,
        // opacity: 0
      }}>
      </div>
      {/* <div>Hi there</div> */}
      <div 
        onClick={handleClick} 
        style={{
        background: 'green',
        height: 50,
        width:50,
        borderRadius:50,
      }}>
      </div>
      <div
        onClick={handleClick}  
        style={{
        background: 'red',
        height: 50,
        width:50,
        borderRadius:50,
      }}>
      </div>                
    </div>  
    )
  }

  export default ColorPicker;