import styled, {css} from 'styled-components/macro';
import {IoMdArrowRoundForward} from 'react-icons/io';
import { IoArrowForward, IoArrowBack } from 'react-icons/io5';
import state from '../state'
const arrowButtons = css`
    width: 50px;
    height: 50px;
    color: #fff;
    cursor: pointer;
    background: #000d1a;
    border-radius: 50px;
    padding: 10px;
    user-select: none;
    margin-right: 2rem;
    transition: 0.3s;

    &:hover{
        background: #cd853f;
        transform : scale(1.05);
    }
`

const NextArrow = styled(IoArrowForward)`
    ${arrowButtons}
`
const PrevArrow = styled(IoArrowBack)`
    ${arrowButtons}
`
const SliderButtons = styled.div`
    position:absolute;
    bottom: 50px;
    right: 50px;
    display: flex;
    z-index: 10;
`;

const CameraButtons = () =>{
    const position ={
        //Tesla
        1:{
            cameraPos: [-27,5,20],
            target:[0,0,4],
            name: "bodyshell_primary_0"
        },
        // Mustang
        2:{
            cameraPos:[5,10,20],
            target:[0,0,4],
            name:"Hull_Hull_0"
        }
    }
    const handleClick = (num) =>{
        state.cameraPos.set(...position[num].cameraPos)
        state.target.set(...position[num].target)
        state.activeMeshName = position[num].name
        state.shouldUpdate = true
    }
    return(
            <>
                <SliderButtons>
                    <PrevArrow onClick={e=>handleClick(2)}/>
                    <NextArrow onClick={e=>handleClick(1)}/>
                </SliderButtons>                
            </>
        )
}

export default CameraButtons;