import { useBox } from 'use-cannon';
const Bulb = props =>{
  
    return(
      <mesh {...props} castShadow>
        <pointLight color='' intensity={[1]}/>
        <sphereBufferGeometry args={[0.8,100,100]}/>
        <meshPhongMaterial emissive='lightblue' />
      </mesh>
  
    )
  }

  export default Bulb;
  